@import url(https://fonts.googleapis.com/css2?family=Antonio:wght@100;200;300;400;500;600&family=Spartan:wght@100;200;300;400;500;600;800&display=swap);
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
}

* {
  outline: none;
}

html {
  font-size: 62.5%;
}
body {
  box-sizing: border-box;
  font-family: "Spartan";
  font-weight: 400;
  line-height: 1.7;
  color: #fff;
  color: var(--color-white);
  background-color: #070724;
  background-color: var(--color-background);
}

/*  Css custom Variables or properties */
:root {
  /* // Main Color */
  --color-background: #070724;
  --color-mercury: #419ebb;
  --color-venus: #eda249;
  --color-earth: #6f2ed6;
  --color-mars: #d14c32;
  --color-jupiter: #d83a34;
  --color-saturn: #cd5120;
  --color-uranus: #1ec2a4;
  --color-neptune: #2d68f0;
  --color-superDark: rgb(7, 7, 36);

  /* // Secondary Color */
  --color-black: #000;
  --color-white: #fff;
  --color-grey-light: #838391;
  --color-grey-dark: #38384f;
  /* //  Gradients */

  /* Height and width are same */
  /* Height and width  of planet */
  --height-mercury: 29rem;
  --height-venus: 40rem;
  --height-earth: 45rem;
  --height-mars: 33.6rem;
  --height-jupiter: 56.2rem;
  --height-saturn: 66.6rem;
  --height-uranus: 45.8rem;
  --height-neptune: 45rem;
}

/* Decreasing the html , font-size value so we can decrease the value of rem */
@media screen and (max-width: 1160px) {
  html {
    font-size: 55%;
  }
}

@media screen and (max-width: 1000px) {
  html {
    font-size: 50%;
  }
}

@media screen and (max-width: 900px) {
  html {
    font-size: 45%;
  }
}

@media only screen and (max-width: 740px) {
  html {
    font-size: 40%;
  }
}

@media only screen and (max-width: 470px) {
  html {
    font-size: 35%;
  }
}

@media only screen and (max-width: 400px) {
  html {
    font-size: 33%;
  }
}

/*--------------------------------------------*/
/* -------------Changing-the-value---------- */
/*--------------------------------------------*/

@media screen and (max-width: 1330px) {
  :root {
    --height-jupiter: 50rem;
    --height-saturn: 60rem;
  }
}

@media screen and (max-width: 1270px) {
  :root {
    --height-jupiter: 45rem;
    --height-saturn: 55rem;
  }
}

@media screen and (max-width: 800px) {
  :root {
    --height-jupiter: 40rem;
    --height-saturn: 50rem;
    --height-venus: 38rem;
    --height-earth: 43rem;
    --height-uranus: 42.8rem;
    --height-neptune: 43rem;
  }
}

